import ReactNativeModal from 'react-native-modal';
import { Button, Text } from 'tradewize-component';
import { styles } from './styles';
import { FlatList, View } from 'react-native';
import { getAvailableLanguages } from '../../../utils';
import RadioButton from 'react-native-animated-radio-button';
import colors from '../../../theme/colors';
import { useEffect, useState } from 'react';
import { changeLanguage } from '../../../i18n/languageManager';
import { RootState } from '../../../redux/store/types';
import { useDispatch, useSelector } from 'react-redux';
import { hideSelectedLanguageModal } from '../../../redux/features/app/actions';
import { useAppTranslation } from '../../../hooks/useAppTranslation';

export interface SelectedLanguageModalProps {
  visible: boolean;
  onClose?: () => void;
}

const SelectedLangue = () => {
  const { t, i18n } = useAppTranslation();
  const dispatch = useDispatch();

  const props = useSelector(
    (state: RootState) => state.app.modalSelectedLanguage,
  );
  const [selectedLanguage, setSelectedLanguage] = useState<string>('');

  const languages = getAvailableLanguages();

  useEffect(() => {
    setSelectedLanguage(i18n.language || 'en');
  }, [i18n.language]);

  const renderItem = ({ item }: { item: { key: string; value: string } }) => {
    return (
      <Button
        size="small"
        variant="ghost"
        onPress={() => setSelectedLanguage(item.key)}
        style={[
          styles.itemContainer,
          selectedLanguage === item.key && styles.selectedItemContainer,
        ]}
      >
        <RadioButton
          style={[
            styles.radioButton,
            selectedLanguage === item.key && styles.selectedRadioButton,
          ]}
          innerBackgroundColor={colors.mainColorPrimary}
          innerContainerStyle={[
            styles.radioButtonInnerContainer,
            selectedLanguage === item.key &&
            styles.selectedRadioButtonInnerContainer,
          ]}
          isActive={selectedLanguage === item.key}
          onPress={() => setSelectedLanguage(item.key)}
        />
        <Text style={styles.itemText}>{item.value}</Text>
      </Button>
    );
  };

  return (
    <ReactNativeModal
      animationIn="zoomIn"
      animationOut="zoomOut"
      style={styles.container}
      isVisible={props?.visible}
      useNativeDriver
      backdropOpacity={0.5}
      backdropColor={colors.black}
    >
      <View style={styles.contentContainer}>
        <Text style={styles.titleText}>{t('PLEASE_SELECT_YOUR_LANGUAGE')}</Text>

        <View style={styles.listContainer}>
          <FlatList
            contentContainerStyle={styles.listContentContainer}
            data={languages}
            keyExtractor={item => item.key}
            renderItem={renderItem}
          />
        </View>

        <Button
          onPress={() => {
            dispatch(hideSelectedLanguageModal());
            props?.onClose?.();
            setTimeout(() => {
              changeLanguage(selectedLanguage);
            }, 150);
          }}
          fullWidth
          style={styles.confirmButton}
        >
          <Text style={styles.buttonText}>{t('CONFIRM')}</Text>
        </Button>
      </View>
    </ReactNativeModal>
  );
};

export default SelectedLangue;
